*{
  box-sizing: border-box;
}

.section-title {
  margin: 0;
  margin-bottom: 48px;

  font-weight: 700;
  font-size: 32px;
  line-height: 100%;
  text-align: left;
  letter-spacing: -1.5px;

  @include on-tablet {
    margin-bottom: 56px;
    font-size: 40px;
    line-height: 40px;
  }

  @include on-desktop {
    font-size: 48px;
  }

  &--aling--left {
    text-align-last: left;
  }
}


