*{
  box-sizing: border-box;
}

.footer {
  background-color: $button-text-color;

  &__content {
    display: grid;
    row-gap: 64px;
    padding-block: 64px;

    @include on-tablet {
      row-gap: 120px;
      padding-block: 120px;
    }
  }

  &__photo {
    background-image: url(/src/images/Timeles2x.png);
    background-size: cover;
    background-position: center;
    min-width: 100%;
    height: 100%;

    @include on-tablet {
      height: 400px;
    }

    @include on-desktop {
      height: 460px;
    }
  }
}
